// hero-section
.hero-section{
    display: flex;
    background-image: url("../images/hero.jpg");
    background-size: cover;
    margin-bottom: 80px;
    height:150vh;


    p{
        text-align: center;
        margin-bottom:0px;
    }
    .main-title{
        font-family: $secondary-font;
        font-weight:400;
        font-size:50px;
        color:#ffffff;
    }
    .publishers-title{
        font-family: $secondary-font;
        font-weight:400;
        font-size:50px;
        color:#ffffff;
    }
    .btn-container{
        text-align: center;

        .contact-us-btn{
            text-decoration: none;
            font-size: 10px;
            border:2px solid black;
            padding:12px 30px;
            font-family:$primary-font;
            font-weight:700;
            color:#2a3c70;
            background-color: white;
            border-color: white;
            display: inline-block;
            position: relative;

            &::after {
                content: "";
                position: absolute;
                width: calc(100% + 20px);
                height: calc(100% + 20px);
                border: 1px solid #fff;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                transition: all 0.3s ease-in-out;
            }

            &:hover {
                &::after {
                    width: 100%;
                    height: 100%;
                }
            }
        }
        
    }
    @media (min-width:768px) {
        
    }

}

// Books-section
    .Books-section{
        margin-top:80px;
        h2{
            text-align: center;
            color:#2f3033;
            font-family:$secondary-font;
            font-weight:400;
            font-size:2.3rem;
        }
        p{
            text-align: center;
            font-family:$primary-font;
            font-weight: 400;
            font-size:0.8rem;
            color: #757575;
            margin-bottom:3rem;
        }
    }
// books-main-section
.books-main-section{
    .container{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }
    .item {
        border:1px solid #c7c7c7;
        padding: $grid-gutter-width / 2;
        margin-bottom: 17px;
        @media (min-width:768px) {
            margin-bottom:0;
        }

        &:hover {
            box-shadow: 0 0 16px rgba(0, 0, 0, 0.2784313725490196);
            transition: 0.5s;
            box-shadow: 0px 12px 10px 2px rgba(25, 54, 118, 0.5);
            transform: translateY(-10px);
        }

        .item-body{
            width: 100%;

            h6{
                font-family:$secondary-font;
                font-weight: 400;
                color:#2b3d72;
                font-size:1.3rem;
                padding-left:10px;
                padding-top: 30px;
                
            }
            .text{
                font-family: $primary-font;
                font-weight:400; //medium
                color:#878787;
                margin-bottom:2px;
                font-size:13px;
                padding-left: 10px;
            }
            .price{
                color:#ee1c24;
                font-family: $primary-font;
                font-family:400;
                font-size:13px;
                padding-left:10px;
            }
            .buy-btn{
                border-radius: 10em;
                padding: 10px 40px;
                font-size: 0.9em;
                text-transform: uppercase;
                color:white;
                background-color: #2b3d72;
                font-family: $primary-font;
                font-weight:600;
                text-decoration: none;
                display: inline-block;
                border: 1px solid #2b3d72;
                transition: all 0.1s ease-in-out;
    
                &:hover{    
                    background: transparent;
                    color: #2b3d72;
                }
            }
            .card{
                border-color: #c7c7c7;
            }
        }
    }
    
}
