.footer-section{
    padding-top:35px;
    background-color: #2b2b33;
    .news-box
    {
        justify-content: center;
        @media (min-width:768px) {
            justify-content: left;
        }
    }
    .news-letter
    {
        text-align: center;
        @media (min-width:768px) {
            text-align: left;
        }
    }
    h6{
        font-family:$primary-font;
        font-size:0.9em;
        font-weight:600;
        color:white;
        font-size:12px;
        width:100%;
        @media (min-width:768px) {
            width:auto;
        }
    }
    img{
        display: inline-block;
        margin: 5px;
        height: 22px;
        width: 22px;
        border-radius: 5px;
    }
    input{
        padding:12px;
        padding-right:50px;
        font-family: $primary-font;
        font-weight:300;
        font-size:13px;
        background-color: #393941;
        color:#5b5b5e;
        border:none;
        margin-right:2px;
        padding-left:30px;
        margin-bottom: 10px;
        margin-right:12px;
        @media (min-width:768px) {
            margin-bottom: 0;
            margin-right:0;
            width:100%;
        }
        @media (min-width:1200px) {
            width:auto;
        }
    }
    .subscribe-btn{
        padding: 11px 30px;
        border:1px solid #959599;
        background-color:#2b2b33;
        color:white;
        font-family:$primary-font;
        font-weight:600;
        font-size:12px;
        float:right;
        text-decoration: none;
        margin-bottom: 10px;
        @media (min-width:768px) {
            width:100%;
            text-align:center;
            margin-top:10px;
        }
        @media (min-width:1200px) {
            width:auto;
            margin-top:0;
        }
        
        
        &:hover{
            background-color:white;
            color:black;
            transition: background-color 0.1s ease-in-out;
        }
    }
    a{
        display: inline;
        font-size:12px;
        font-family: $primary-font;
        font-weight:400;
        color: #878788;
        text-decoration: none;
    }
    .services{
        border:1px solid #44444a;
        text-align:center;
        background-color: #2b2b33;
        height:100%;
        display: flex;
        justify-content: center;
        border-left: none;
        border-right: none;
        }
        .service{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding: 0 10px;
            

            a{
                padding-right:20px;
                @media (min-width:992px) {
                    padding:10px;
                }
                &:hover{
                    color:white;
                    transition: color 0.1s ease-in-out;
                    width:100%;
                }
        }
    }
    img{
        margin:5px;
    }

    .social-icons
    {
        .fab{
            font-size: 35px;
        }
    }
    
}

